import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { IGUIModel } from "app/shared/interfaces/user-views";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";
import { CandidateTrainingDialogComponent } from "../candidate-training-dialog/candidate-training-dialog.component";
import { CandidateTrainingService } from "app/recruitment/services/candidate/education/candidate-training.service";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { IResponse } from "app/shared/interfaces/responces";
import {
  ICandidateTrainingDisplayModel,
  ICandidateTrainingModel,
} from "app/recruitment/models/candidate/education/training";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { candidateTrainingToDisplay } from "app/recruitment/mapper/candidate/candidate-training";
@Component({
  selector: "candidate-training-card",
  templateUrl: "./candidate-training-card.component.html",
  styleUrls: ["./candidate-training-card.component.scss"],
})
export class CandidateTrainingCardComponent
  extends AppHelpers
  implements OnInit
{
  isLoaded: boolean = false;
  allowedSections: IGUIModel[];
  @Output() onClickIcon? = new EventEmitter<any>();
  isTableView: boolean = false;
  candidateId: number = 0;
  trainingData: ICandidateTrainingDisplayModel;
  @Input() isUpdate: boolean = false;

  constructor(private candidateTrainingService: CandidateTrainingService) {
    super();
    this.candidateId = CandidateService.candidateInstanceId$.getValue();
  }

  ngOnInit(): void {}

  override onModulesChanged() {
    this.allowedSections = this.getCustomViewChildren(
      this.getParentViewName,
      ViewsManager.candidate_training_info_sec
    );
  }

  override onChangeBusinessAndCompanyId() {
    if (this.candidateId > 0 && this.checkNoBusinessOrCompany()) {
      this.getAll();
    }
  }

  getAll() {
    this.isLoaded = false;
    this.candidateTrainingService
      .getAll(CandidateService.candidateInstanceId$.getValue())
      .subscribe((res: IResponse<ICandidateTrainingModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          let trainingTodisplay = candidateTrainingToDisplay(res.data);
          this.trainingData = trainingTodisplay[0];

          this.candidateTrainingService.trainingData$.next(trainingTodisplay);
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }


  click($event) {
    this.onClickIcon.emit($event);
  }

  toggleTable() {
    this.isTableView = !this.isTableView;
  }

  showEdit($event) {
    this.isUpdate = $event;

}
}
