import { Injectable } from "@angular/core";
import {
  ICandidateMilitaryModel,
  ICandidateMilitaryRequest,
} from "app/recruitment/models/candidate/general-info/military-data";
import { IResponse } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { AppClientService } from "app/shared/services/app-client.service";
import { BehaviorSubject } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class CandidateMilitaryDataService extends AppClientService {
  militaryData$ = new BehaviorSubject<ICandidateMilitaryModel>(undefined);

  constructor() {
    super();
  }

  create(data: ICandidateMilitaryRequest) {
    return this.post<IResponse<ICandidateMilitaryModel>>(
      "Recruitment/MilitaryStatus/Add",
      data
    );
  }

  update(data: ICandidateMilitaryRequest) {
    return this.post<IResponse<ICandidateMilitaryModel>>(
      "Recruitment/MilitaryStatus/Edit",
      data
    );
  }

  getAll(candidateId: number) {
    return this.get<IResponse<ICandidateMilitaryModel[]>>(
      "Recruitment/MilitaryStatus/GetAll",
      {
        params: {
          $filter: `PersonID eq ${candidateId}`,
          $expand: `${db.comm_Military_Status},${db.comm_Military_Grade}`

        },
      }
    );
  }
}
