import { Injectable } from "@angular/core";
import {
  ICandidateTrainingDisplayModel,
  ICandidateTrainingModel,
  ICandidateTrainingRequest,
} from "app/recruitment/models/candidate/education/training";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { BehaviorSubject } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class CandidateTrainingService extends AppClientService {
  trainingData$=new BehaviorSubject<ICandidateTrainingDisplayModel[]>(undefined)
  constructor() {
    super();
  }

  create(data: ICandidateTrainingRequest) {
    return this.post<IResponse<ICandidateTrainingModel>>(
      "Recruitment/Training/Add",
      data
    );
  }

  update(data: ICandidateTrainingRequest) {
    return this.post<IResponse<ICandidateTrainingModel>>(
      "Recruitment/Training/Edit",
      data
    );
  }

  getAll(candidateId: number) {
    return this.get<IResponse<ICandidateTrainingModel[]>>(
      "Recruitment/Training/GetAll",
      {
        params: {
          $filter: `Person_Instance_ID eq ${candidateId}`,
        },
      }
    );
  }

  Delete(id: number) {
    return this.delete<IResponse>("Recruitment/Training/Delete", {
      params: { ID: id },
    });
  }
}
