import { Component, EventEmitter, OnInit, Output } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { ITabDescriptionInfo } from "app/recruitment/components/candidate/add-candidate/shared-candidate-components/candidate-tab-description/candidate-tab-description.component";
import { ICandidateRequest } from "app/recruitment/models/candidate/candidate";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { Constants } from "app/shared/resources/constants";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { CustomValidators } from "ngx-custom-validators";

@Component({
  selector: "candidate-general-info-form",
  templateUrl: "./candidate-general-info-form.component.html",
  styleUrls: ["./candidate-general-info-form.component.scss"],
})
export class CandidateGeneralInfoFormComponent
  extends AppHelpers
  implements OnInit
{
  candidateId: number = 0;
  // @Output() changeCandidateId = new EventEmitter<number>();
  @Output() changeDescription = new EventEmitter<ITabDescriptionInfo>();

  form: FormGroup;
  submitted: boolean = false;
  constants = Constants;
  isView: boolean = false;
  @Output() isUpdate = new EventEmitter<boolean>();
  constructor(private candidateService: CandidateService) {
    super();

    this.candidateService.submitted$.subscribe(
      (submitted) => (this.submitted = submitted)
    );
    this.candidateId = CandidateService.candidateId$.getValue();
  }

  override onChangeBusinessAndCompanyId() {
    if (this.candidateId > 0 && this.checkNoBusinessOrCompany()) {
      this.getCandidateById();
    } else this.invalidRequiredCount();
  }

  ngOnInit() {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      ar_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      en_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      // code: [null, [Validators.required, CustomValidator.whiteSpace]],
      selectedCountry: [null],
      national_Id: [
        null,
        [CustomValidators.number, CustomValidator.Length(Constants.nationalId)],
      ],
      birthDate: [null],
      hireDate: [null, [Validators.required]],
      gender_ID: [null],
      religioN_ID: [null],
      resource_Type: [null],
      resource_Name: [null],
    });

    this.invalidRequiredCount();
  }

  get formCtrls() {
    return this.form.controls;
  }

  getCandidateById() {
    this.showLoading();
    // this.candidateService
    //   .getById(this.candidateId)
    //   .subscribe((res: IResponse<ICandidateListModel[]>) => {
    //     if ((res.message.messageType = MessageTypes.Success)) {
    // this.form.patchValue(res.data[0]);
    // this.changeDescriptionInfo(true, 0);
    //   } else this.errorList(res?.message?.messages);
    //   this.hideLoading();
    // });

    this.candidateService.candidate$.subscribe((can) => {
      this.form.patchValue(can);
      this.formCtrls.selectedCountry.setValue(can.nationality.id);
      this.formCtrls.gender_ID.setValue(can.gender.id);
      this.formCtrls.religioN_ID.setValue(can.religion.id);
      this.formCtrls.resource_Type.setValue(can.resource_Type.id);

      this.changeDescriptionInfo(true, 0);
      this.hideLoading();
    });
  }

  submit() {
    // console.log(this.form.value);
    // this.submitted = true;
    // if (this.form.valid) {
    const model: ICandidateRequest = {
      // id: this.form.value.id,
      Core_Person: {
        ar_Name: this.form.value.ar_Name,
        en_Name: this.form.value.en_Name,
        code:Math.floor(Math.random()*10000).toString(),
        nationalid: this.form.value.national_Id,
        dob: HelpersFunctions.mapDateReverse(this.form.value.birthDate),
        hireDate: HelpersFunctions.mapDateReverse(this.form.value.hireDate),
        gender_ID: this.form.value.gender_ID,
        religioN_ID: this.form.value.religioN_ID,
      },
      selectedCountry: this.form.value.selectedCountry,
      resource_Type: this.form.value.resource_Type,
      resource_Name: this.form.value.resource_Name,
    };


    this.candidateService.candidateData$.next({
      ...this.candidateService.candidateData$.getValue(),
      GeneralData: model,
    });

    // model.id == 0 ? this.create(model) : this.update(model);
    // }
    // else this.toastr.error("General.formNotValid");
  }

  // create(model: ICandidateRequest) {
  //   this.showLoading();
  //   this.candidateService
  //     .create(model)
  //     .subscribe((res: IResponse<ICandidateModel>) => {
  //       if (res?.message?.messageType == MessageTypes.Success) {
  //         this.addSuccessfullyMsg(res?.message?.messageTypeName);
  //         this.candidateId = res.data.id;
  //         this.changeCandidateId.emit(this.candidateId);
  //         this.formCtrls.id.setValue(this.candidateId);
  //         let url = `/recruitment/${this.moduleId}/candidate/edit/${this.candidateId}`;
  //         this.navigateTo(url);
  //       } else this.errorList(res?.message?.messages);
  //       this.hideLoading();
  //     });
  // }

  // update(model: ICandidateRequest) {
  //   this.showLoading();
  //   this.candidateService
  //     .update(model)
  //     .subscribe((res: IResponse<ICandidateModel>) => {
  //       if (res.message.messageType == MessageTypes.Success) {
  //         this.updateSuccessfullyMsg(res?.message?.messageTypeName);
  //       } else this.errorList(res?.message?.messages);
  //       this.hideLoading();
  //     });
  // }

  // clearForm() {
  //   this.form.reset();
  //   this.submitted = false;
  // }

  //descriptionInfo: ITabDescriptionInfo;

  changeDescriptionInfo(isDone: boolean, noOfRequiredInputs: number) {
    this.changeDescription.emit({
      isDone: isDone,
      noOfRequiredInputs: noOfRequiredInputs,
    });
  }

  invalidRequiredCount() {
    // if (this.candidateId == 0) {
    let count = HelpersFunctions.noOfInvalidAndRequiredInputs(this.form);
    this.changeDescriptionInfo(count == 0 ? true : false, count);

    this.candidateService.isDone$.next({
      ...this.candidateService.isDone$.getValue(),
      //GeneralData: this.descriptionInfo.isDone,
      GeneralData: count == 0 ? true : false,
    });
    //if (this.descriptionInfo.isDone) this.submit();
    // }
  }

  updateDataInService() {
    setTimeout(() => this.submit(), 1000);
  }

  changeToEdit() {
    if (this.isUpdate ) {
      this.isUpdate.emit(false);
    }
  }
}
