import { Component, EventEmitter, OnInit, Output } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { ITabDescriptionInfo } from "app/recruitment/components/candidate/add-candidate/shared-candidate-components/candidate-tab-description/candidate-tab-description.component";
import { ICandidateMilitaryRequest } from "app/recruitment/models/candidate/general-info/military-data";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { IMilitaryStatusModel } from "app/system-setup/hr-setup/models/military";
import { CustomValidators } from "ngx-custom-validators";
import { CandidateMilitaryDataService } from "app/recruitment/services/candidate/general-info/candidate-military-data.service";

@Component({
  selector: "candidate-basic-military",
  templateUrl: "./candidate-basic-military.component.html",
  styleUrls: ["./candidate-basic-military.component.scss"],
})
export class CandidateBasicMilitaryComponent
  extends AppHelpers
  implements OnInit
{
  candidateId: number = 0;
  @Output() changeDescriptionInfo = new EventEmitter<ITabDescriptionInfo>();

  form: FormGroup;
  submitted: boolean = false;

  selectedStatus: IMilitaryStatusModel;

  constructor(
    private candidateService: CandidateService,
    private candidateMilitaryDataService: CandidateMilitaryDataService
  ) {
    super();

    this.candidateService.submitted$.subscribe(
      (submitted) => (this.submitted = submitted)
    );
    this.candidateId = CandidateService.candidateId$.getValue();
  }

  override onChangeBusinessAndCompanyId() {
    if (this.candidateId > 0 && this.checkNoBusinessOrCompany()) {
      this.getMilitaryStatus();
    }
  }

  ngOnInit() {
    this.form = this.fb.group({
      // id: [0, { nonNullable: true }],
      militaryID: [null, [Validators.required]],
      gradeID: [null],
      statusDate: [null, [CustomValidators.date]],
    });

    this.invalidRequiredCount();
  }

  get formCtrls() {
    return this.form.controls;
  }

  getMilitaryStatus() {
    this.showLoading();
    this.candidateMilitaryDataService.militaryData$.subscribe((mil) => {
      if (mil != null) {
        this.form.patchValue(mil);

        this.changeDescription(true, 0);
      } else this.invalidRequiredCount();
      this.hideLoading();
    });
  }

  submit() {
    // console.log(this.form.value);
    // this.submitted = true;
    // if (this.form.valid) {
    const model: ICandidateMilitaryRequest = {
      // personID: this.candidateId,
      militaryID: this.form.value.militaryID,
      statusDate: HelpersFunctions.mapDateReverse(this.form.value.statusDate),
    };
    if (this.selectedStatus.showGrade) model.gradeID = this.form.value.gradeID;

    this.candidateService.candidateData$.next({
      ...this.candidateService.candidateData$.getValue(),
      MilitaryData: model,
    });

    // this.formCtrls.id.value == 0 ? this.create(model) : this.update(model);
    // }
    // else this.toastr.error("General.formNotValid");
  }

  // create(model: ICandidateMilitaryRequest) {
  //   this.showLoading();
  //   this.candidateMilitaryDataService
  //     .create(model)
  //     .subscribe((res: IResponse<ICandidateMilitaryModel>) => {
  //       if (res?.message?.messageType == MessageTypes.Success) {
  //         this.addSuccessfullyMsg(res?.message?.messageTypeName);
  //         this.formCtrls.id.setValue(res.data.militaryID);
  //         this.changeDescription(true, 0);
  //       } else this.errorList(res.message.messages);
  //       this.hideLoading();
  //     });
  // }

  // update(model: ICandidateMilitaryRequest) {
  //   this.showLoading();
  //   this.candidateMilitaryDataService
  //     .update(model)
  //     .subscribe((res: IResponse<ICandidateMilitaryModel>) => {
  //       if (res.message.messageType == MessageTypes.Success) {
  //         this.updateSuccessfullyMsg(res?.message?.messageTypeName);
  //       } else this.errorList(res.message.messages);
  //       this.hideLoading();
  //     });
  // }

  onChangeStatus(values: IMilitaryStatusModel[]) {
    this.selectedStatus = values?.length > 0 ? values[0] : null;
    if (this.selectedStatus?.showGrade)
      this.formCtrls.gradeID.setValidators([Validators.required]);
    else this.formCtrls.gradeID.clearValidators();
    this.formCtrls.gradeID.updateValueAndValidity();

    this.invalidRequiredCount();
  }

  changeDescription(isDone: boolean, noOfRequiredInputs: number) {
    this.changeDescriptionInfo.emit({
      isDone: isDone,
      noOfRequiredInputs: noOfRequiredInputs,
    });
  }

  invalidRequiredCount() {
    // if (!(this.formCtrls.id.value > 0)) {
    let count = HelpersFunctions.noOfInvalidAndRequiredInputs(this.form);
    this.changeDescription(count == 0 ? true : false, count);

    this.candidateService.isDone$.next({
      ...this.candidateService.isDone$.getValue(),
      MilitaryData: count == 0 ? true : false,
    });
    // }
  }

  updateDataInService() {
    setTimeout(() => this.submit(), 1000);
  }
}
