import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { ITabDescriptionInfo } from "app/recruitment/components/candidate/add-candidate/shared-candidate-components/candidate-tab-description/candidate-tab-description.component";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "candidate-basic-info",
  templateUrl: "./candidate-basic-info.component.html",
  styleUrls: ["./candidate-basic-info.component.scss"],
})
export class CandidateBasicInfoComponent extends AppHelpers implements OnInit {
  candidateId: number = 0;
  @Input() isView:boolean=false;
  @Output() isUpdate = new EventEmitter<boolean>();

  constructor() {

    super();
    this.candidateId = CandidateService.candidateId$.getValue();

  }

  ngOnInit() {}

  descriptionInfo: ITabDescriptionInfo;
  descriptionMaritalLastInfo: ITabDescriptionInfo;
  descriptionMilitaryLastInfo: ITabDescriptionInfo;

  changeDescriptionInfo(info: ITabDescriptionInfo, tabIndex: number) {
    if (tabIndex == 1) this.descriptionMaritalLastInfo = info;
    else this.descriptionMilitaryLastInfo = info;
    this.descriptionInfo = {
      isDone:
        this.descriptionMilitaryLastInfo?.isDone &&
        this.descriptionMaritalLastInfo?.isDone,
      noOfRequiredInputs:
        this.descriptionMilitaryLastInfo?.noOfRequiredInputs +
        this.descriptionMaritalLastInfo?.noOfRequiredInputs,
    };

  }
  submit(){
    if (this.candidateId > 0) {
      this.isUpdate.emit(false);
    }
  }
}
