import { Component, OnInit } from "@angular/core";
import { ICandidateAddressModel } from "app/recruitment/models/candidate/general-info/address";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { CandidateAddressService } from "app/recruitment/services/candidate/general-info/candidate-address.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "candidate-contact-address-card",
  templateUrl: "./candidate-contact-address-card.component.html",
  styleUrls: ["./candidate-contact-address-card.component.scss"],
})
export class CandidateContactAddressCardComponent
  extends AppHelpers
  implements OnInit
{
  isLoaded: boolean = false;
  candidateId: number = 0;
addressData:ICandidateAddressModel;
  constructor(private candidateAddressService: CandidateAddressService) {
    super();
    this.candidateId=CandidateService.candidateId$.getValue();
  }

  ngOnInit(): void {}
  override onChangeBusinessAndCompanyId() {
    if (this.candidateId > 0 && this.checkNoBusinessOrCompany()) {
      this.getAddress();
    }
  }

  getAddress() {
    this.candidateAddressService
      .getAll(this.candidateId)
      .subscribe((res: IResponse<ICandidateAddressModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          if (res.data.length > 0) {
            this.addressData=res.data[0];
            this.candidateAddressService.candidateAddress$.next(res.data[0]);
            this.isLoaded=true;
          }
        } else this.errorList(res.message.messages);
      });
  }
}
