import { Injectable } from "@angular/core";
import {
  ICandidateContactDeleteParams,
  ICandidateContactDisplayModel,
  ICandidateContactModel,
  ICandidateContactRequest,
} from "app/recruitment/models/candidate/general-info/contacts";
import { IResponse } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { AppClientService } from "app/shared/services/app-client.service";
import { BehaviorSubject } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class CandidateContactsService extends AppClientService {
  contactData$=new BehaviorSubject<ICandidateContactDisplayModel[]>(undefined)
  constructor() {
    super();
  }

  create(data: ICandidateContactRequest) {
    return this.post<IResponse<ICandidateContactModel>>(
      "Recruitment/Contacts/Add",
      data
    );
  }

  update(data: ICandidateContactRequest) {
    return this.post<IResponse<ICandidateContactModel>>(
      "Recruitment/Contacts/Edit",
      data
    );
  }

  getAll(candidateId: number) {
    return this.get<IResponse<ICandidateContactModel[]>>(
      "Recruitment/Contacts/GetAll",
      {
        params: {
          $filter: `Person_ID eq ${candidateId}`,
          $expand: `${db.comm_Contact},${db.comm_Contact}.${db.comm_Contact_Type}`,
        },
      }
    );
  }

  Delete(params: ICandidateContactDeleteParams) {
    return this.delete<IResponse>("Recruitment/Contacts/Delete", {
      params: params,
    });
  }
}
