import { Component, EventEmitter, OnInit, Output } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { ITabDescriptionInfo } from "app/recruitment/components/candidate/add-candidate/shared-candidate-components/candidate-tab-description/candidate-tab-description.component";
import { ICandidateMaritalRequest } from "app/recruitment/models/candidate/general-info/marital-data";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { CandidateMaritalDataService } from "app/recruitment/services/candidate/general-info/candidate-marital-data.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { CustomValidators } from "ngx-custom-validators";

@Component({
  selector: "candidate-basic-marital",
  templateUrl: "./candidate-basic-marital.component.html",
  styleUrls: ["./candidate-basic-marital.component.scss"],
})
export class CandidateBasicMaritalComponent
  extends AppHelpers
  implements OnInit
{
  candidateId: number = 0;
  @Output() changeDescriptionInfo = new EventEmitter<ITabDescriptionInfo>();
  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;
  constructor(
    private candidateService: CandidateService,
    private candidateMaritalDataService: CandidateMaritalDataService
  ) {
    super();

    this.candidateService.submitted$.subscribe(
      (submitted) => (this.submitted = submitted)
    );
    this.candidateId = CandidateService.candidateId$.getValue();
  }

  override onChangeBusinessAndCompanyId() {
    if (this.candidateId > 0 && this.checkNoBusinessOrCompany()) {
      this.getMaritalStatus();
    }
  }

  ngOnInit() {
    this.form = this.fb.group({
      // id: [0, { nonNullable: true }],
      maritalID: [null, [Validators.required]],
      statusDate: [null, [CustomValidators.date]],
    });

    this.invalidRequiredCount();
  }

  get formCtrls() {
    return this.form.controls;
  }

  getMaritalStatus() {
    this.showLoading();

    this.candidateMaritalDataService.maritalData$.subscribe((ma) => {
      if (ma != null) {
        this.form.patchValue(ma);
        this.changeDescription(true, 0);
        this.isLoaded = true;
      } else this.invalidRequiredCount();
      this.hideLoading();
    });
  }

  submit() {
    // console.log(this.form.value);
    // this.submitted = true;
    // if (this.form.valid) {
    const model: ICandidateMaritalRequest = {
     // personID: this.candidateId,
      maritalID: this.form.value.maritalID,
      statusDate: HelpersFunctions.mapDateReverse(this.form.value.statusDate),
    };

    this.candidateService.candidateData$.next({
      ...this.candidateService.candidateData$.getValue(),
      MaritalData: model,
    });

    // this.formCtrls.id.value == 0 ? this.create(model) : this.update(model);
    // }
    // else this.toastr.error("General.formNotValid");
  }

  // create(model: ICandidateMaritalRequest) {
  //   this.showLoading();
  //   this.candidateMaritalDataService
  //     .create(model)
  //     .subscribe((res: IResponse<ICandidateMaritalModel>) => {
  //       if (res?.message?.messageType == MessageTypes.Success) {
  //         this.addSuccessfullyMsg(res?.message?.messageTypeName);
  //         this.formCtrls.id.setValue(res.data.maritalID);
  //         this.changeDescription(true, 0);
  //       } else this.errorList(res.message.messages);
  //       this.hideLoading();
  //     });
  // }

  update(model: ICandidateMaritalRequest) {
    this.showLoading();
    this.candidateMaritalDataService
      .update(model)
      .subscribe((res: IResponse<ICandidateMaritalRequest>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  changeDescription(isDone: boolean, noOfRequiredInputs: number) {
    this.changeDescriptionInfo.emit({
      isDone: isDone,
      noOfRequiredInputs: noOfRequiredInputs,
    });
  }

  invalidRequiredCount() {
    // if (!(this.formCtrls.id.value > 0)) {
    let count = HelpersFunctions.noOfInvalidAndRequiredInputs(this.form);
    this.changeDescription(count == 0 ? true : false, count);

    this.candidateService.isDone$.next({
      ...this.candidateService.isDone$.getValue(),
      MaritalData: count == 0 ? true : false,
    });
    // }
  }

  updateDataInService() {
    setTimeout(() => this.submit(), 1000);
  }
}
