import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { IGUIModel } from "app/shared/interfaces/user-views";
import { ViewsManager } from "app/shared/resources/views-manager";
import { ICandidateListModel } from "app/recruitment/models/candidate/candidate";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";

@Component({
  selector: "candidate-general-info-card",
  templateUrl: "./candidate-general-info-card.component.html",
  styleUrls: ["./candidate-general-info-card.component.scss"],
})
export class CandidateGeneralInfoCardComponent
  extends AppHelpers
  implements OnInit
{
  isLoaded: boolean = false;
  candidate: ICandidateListModel;
  allowedSections: IGUIModel[];
  @Input() isUpdate: boolean = false;

  @Output() onClickIcon? = new EventEmitter<any>();

  constructor(public candidateService: CandidateService) {
    super();
  }

  ngOnInit(): void {
    this.candidateService.candidate$.subscribe((can) => {
      this.candidate = can;
      if (this.candidate != null) this.isLoaded = true;
    });
    // if (this.candidate != null) this.isLoaded = true;
    // console.log("gg", this.candidate);
  }

  override onModulesChanged() {
    this.allowedSections = this.getCustomViewChildren(
      this.getParentViewName,
      ViewsManager.candidate_general_info_tab
    );
  }

  click($event) {
    this.onClickIcon.emit($event);
  }

  showEdit($event) {
    if (this.isUpdate) {
      this.isUpdate = $event;
    }
  }
}
