import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { IGUIModel } from "app/shared/interfaces/user-views";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";
import {
  ICandidateWorkExperienceDisplayModel,
  ICandidateWorkExperienceModel,
} from "app/recruitment/models/candidate/work-experience/work-experience";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { CandidateWorkExperienceService } from "app/recruitment/services/candidate/work-experience/candidate-work-experience.service";
import { IResponse } from "app/shared/interfaces/responces";
import { candidateWorkExperienceToDisplay } from "app/recruitment/mapper/candidate/candidate-work-experience";
import { MessageTypes } from "app/shared/enums/message-types.enum";
@Component({
  selector: "candidate-work-experience-card",
  templateUrl: "./candidate-work-experience-card.component.html",
  styleUrls: ["./candidate-work-experience-card.component.scss"],
})
export class CandidateWorkExperienceCardComponent
  extends AppHelpers
  implements OnInit
{
  isLoaded: boolean = false;
  allowedSections: IGUIModel[];
  @Output() onClickIcon? = new EventEmitter<any>();
  isTableView: boolean = false;
  candidateId: number = 0;
  workecperienceData: ICandidateWorkExperienceDisplayModel;
  @Input() isUpdate: boolean = false;
  resignationReasonOtherId: number = 6;
  constructor(
    private candidateWorkExperienceService: CandidateWorkExperienceService
  ) {
    super();
    this.candidateId = CandidateService.candidateInstanceId$.getValue();
  }
  ngOnInit(): void {}
  override onModulesChanged() {
    this.allowedSections = this.getCustomViewChildren(
      this.getParentViewName,
      ViewsManager.candidate_Working_Experience_info_sec
    );
  }
  override onChangeBusinessAndCompanyId() {
    if (this.candidateId > 0 && this.checkNoBusinessOrCompany()) {
      this.getAll();
    }
  }

  getAll() {
    this.isLoaded = false;
    this.candidateWorkExperienceService
      .getAll(this.candidateId)
      .subscribe((res: IResponse<ICandidateWorkExperienceModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          let workecperienceToDisplay = candidateWorkExperienceToDisplay(
            res.data,
            this.resignationReasonOtherId
          );
          this.workecperienceData = workecperienceToDisplay[0];
          this.candidateWorkExperienceService.workecperienceData$.next(
            workecperienceToDisplay
          );

          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  click($event) {
    this.onClickIcon.emit($event);
  }
  toggleTable() {
    this.isTableView = !this.isTableView;
  }

  showEdit($event) {
    this.isUpdate = $event;
  }
}
