import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { IGUIModel } from "app/shared/interfaces/user-views";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";
import {
  ICandidateCertificationDisplayModel,
  ICandidateCertificationModel,
} from "app/recruitment/models/candidate/education/certifications";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { CandidateCertificationsService } from "app/recruitment/services/candidate/education/candidate-certifications.service";
import { IResponse } from "app/shared/interfaces/responces";
import { candidateCertificationToDisplay } from "app/recruitment/mapper/candidate/candidate-certification";
import { MessageTypes } from "app/shared/enums/message-types.enum";
@Component({
  selector: "candidate-certification-card",
  templateUrl: "./candidate-certification-card.component.html",
  styleUrls: ["./candidate-certification-card.component.scss"],
})
export class CandidateCertificationCardComponent
  extends AppHelpers
  implements OnInit
{
  isLoaded: boolean = false;
  allowedSections: IGUIModel[];
  @Output() onClickIcon? = new EventEmitter<any>();
  isTableView: boolean = false;
  candidateId: number = 0;
  certificationData: ICandidateCertificationDisplayModel;
  @Input() isUpdate: boolean = false;

  constructor(
    private cndidateCertificationsService: CandidateCertificationsService
  ) {
    super();
    this.candidateId = CandidateService.candidateInstanceId$.getValue();
  }
  ngOnInit(): void {}

  override onModulesChanged() {
    this.allowedSections = this.getCustomViewChildren(
      this.getParentViewName,
      ViewsManager.candidate_certification_info_sec
    );
  }

  override onChangeBusinessAndCompanyId() {
    if (this.candidateId > 0 && this.checkNoBusinessOrCompany()) {
      this.getAll();
    }
  }

  getAll() {
    this.isLoaded = false;
    this.cndidateCertificationsService
      .getAll(this.candidateId)
      .subscribe((res: IResponse<ICandidateCertificationModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          let certificationTodisplay = candidateCertificationToDisplay(
            res.data
          );
          this.certificationData = certificationTodisplay[0];
          this.cndidateCertificationsService.certificationsData$.next(
            certificationTodisplay
          );
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  click($event) {
    this.onClickIcon.emit($event);
  }

  toggleTable() {
    this.isTableView = !this.isTableView;
  }

  showEdit($event) {
    this.isUpdate = $event;
  }
}
