import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ITabDescriptionInfo } from "app/recruitment/components/candidate/add-candidate/shared-candidate-components/candidate-tab-description/candidate-tab-description.component";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";

@Component({
  selector: "candidate-work-experience-info",
  templateUrl: "./candidate-work-experience-info.component.html",
  styleUrls: ["./candidate-work-experience-info.component.scss"],
})
export class CandidateWorkExperienceInfoComponent
  extends AppHelpers
  implements OnInit
{
  candidateId: number = 0;

  @Input() isView: boolean = false;
  descriptionInfo: ITabDescriptionInfo;
  @Output() isUpdate = new EventEmitter<boolean>();

  constructor() {
    super();
    this.candidateId = CandidateService.candidateId$.getValue();
  }

  ngOnInit(): void {}

  changeDescriptionInfo(info: ITabDescriptionInfo, noOfRequiredInputs: number) {
    this.descriptionInfo = {
      isDone: info.isDone,
      noOfRequiredInputs: noOfRequiredInputs,
    };
  }

  submit() {
    this.isUpdate.emit(false);
  }}
