import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { IGUIModel } from "app/shared/interfaces/user-views";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { CandidateEducationService } from "app/recruitment/services/candidate/education/candidate-education.service";
import {
  ICandidateEducationDisplayModel,
  ICandidateEducationModel,
} from "app/recruitment/models/candidate/education/education";
import { candidateEducationToDisplay } from "app/recruitment/mapper/candidate/candidate-education";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
@Component({
  selector: "candidate-education-card",
  templateUrl: "./candidate-education-card.component.html",
  styleUrls: ["./candidate-education-card.component.scss"],
})
export class CandidateEducationCardComponent
  extends AppHelpers
  implements OnInit
{
  isLoaded: boolean = false;
  allowedSections: IGUIModel[];
  @Output() onClickIcon? = new EventEmitter<any>();
  isTableView: boolean = false;
  candidateId: number = 0;
  educationData: ICandidateEducationDisplayModel;
  @Input() isUpdate: boolean = false;

  constructor(private candidateEducationService: CandidateEducationService) {
    super();
    this.candidateId = CandidateService.candidateId$.getValue();
  }
  ngOnInit(): void {}

  override onModulesChanged() {
    this.allowedSections = this.getCustomViewChildren(
      this.getParentViewName,
      ViewsManager.candidate_education_info_sec
    );
  }
  override onChangeBusinessAndCompanyId() {
    if (this.candidateId > 0 && this.checkNoBusinessOrCompany()) {
      this.getAll();
    }
  }

  click($event) {
    this.onClickIcon.emit($event);
  }
  toggleTable() {
    this.isTableView = !this.isTableView;
  }

  getAll() {
    this.isLoaded = false;
    this.candidateEducationService
      .getAll(this.candidateId)
      .subscribe((res: IResponse<ICandidateEducationModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          let educationTodusplay = candidateEducationToDisplay(res.data);

          this.candidateEducationService.educationData$.next(
            educationTodusplay
          );
          this.educationData = educationTodusplay[0];
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  showEdit($event) {
      this.isUpdate = $event;

  }
}
