import { Injectable } from "@angular/core";
import {
  ICandidateDocumentsDisplayModel,
  ICandidateDocumentsModel,
  ICandidateDocumentsRequest,
} from "app/recruitment/models/candidate/work-experience/documents";
import { IResponse } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { AppClientService } from "app/shared/services/app-client.service";
import { BehaviorSubject } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class CandidateDocumentsService extends AppClientService {
  documentData$=new BehaviorSubject<ICandidateDocumentsDisplayModel[]>(undefined)

  constructor() {
    super();
  }

  create(data: ICandidateDocumentsRequest) {
    return this.post<IResponse<ICandidateDocumentsModel>>(
      "Recruitment/Documents/Add",
      data
    );
  }

  update(data: ICandidateDocumentsRequest) {
    return this.post<IResponse<ICandidateDocumentsModel>>(
      "Recruitment/Documents/Edit",
      data
    );
  }

  getAll(candidateId: number) {
    return this.get<IResponse<ICandidateDocumentsModel[]>>(
      "Recruitment/Document/GetAll",
      {
        params: {
          $filter: `Person_Instance_ID eq ${candidateId}`,
          $expand: `${ db.Comm_Attachment}.${ db.Comm_Attachment_Type}` ,
        },
      }
    );
  }

  Delete(id: number) {
    return this.delete<IResponse>("Recruitment/Document/Delete", {
      params: { ID: id },
    });
  }
}
