import { IAttachmentTypesModel } from "app/system-setup/general-setup/models/attachment-types";

export interface IFile {
  name: string;
  size: number;
  type: string;
  LastModified: number;
  LastModifiedDate: Date;
}

export interface IFileBase64 extends IFile {
  FileAsBase64: string;
}

export interface IFileBinary {
  binaryData: ArrayBuffer;
  hexString: string;
}

// use in file uploader > display existing files
export interface IFileData {
  id: number;
  path: string;
  name: string;
  type: string;
  size: number;
  extra?: string;
}

export interface IFileApiModel {
  id: number;
  // ar_Name: string;
  en_Name: string;
  path: string;
  type_ID: number;
  size: number;
  mime: string;
  // fileType: null;
  last_Update_Date:Date;
  comm_Attachment_Type: IAttachmentTypesModel;
}
