import { Injectable } from "@angular/core";
import {
  ICandidateAddressModel,
  ICandidateAddressRequest,
} from "app/recruitment/models/candidate/general-info/address";
import { IResponse } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { AppClientService } from "app/shared/services/app-client.service";
import { BehaviorSubject } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class CandidateAddressService extends AppClientService {
  candidateAddress$=new BehaviorSubject<ICandidateAddressModel>(undefined);
  constructor() {
    super();
  }

  create(data: ICandidateAddressRequest) {
    return this.post<IResponse<ICandidateAddressModel>>(
      "Recruitment/Address/Add",
      data
    );
  }

  update(data: ICandidateAddressRequest) {
    return this.post<IResponse<ICandidateAddressModel>>(
      "Recruitment/Address/Edit",
      data
    );
  }

  getAll(candidateId: number) {
    return this.get<IResponse<ICandidateAddressModel[]>>(
      "Recruitment/Address/GetAll",
      {
        params: {
          $filter: `Person_ID eq ${candidateId}`,
          $expand: `${db.comm_Address}.${db.comm_Governorate}.${db.comm_Country}`,
        },
      }
    );
  }
}
