import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { ICandidateWorkExperienceDisplayModel } from "app/recruitment/models/candidate/work-experience/work-experience";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { CandidateWorkExperienceService } from "app/recruitment/services/candidate/work-experience/candidate-work-experience.service";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";
@Component({
  selector: "candidate-work-experience-table",
  templateUrl: "./candidate-work-experience-table.component.html",
  styleUrls: ["./candidate-work-experience-table.component.scss"],
})
export class CandidateWorkExperienceTableComponent
  extends AppHelpers
  implements OnInit
{
  @Input() isMatPaginator: boolean = false;
  @Output() afterDelete =
    new EventEmitter<ICandidateWorkExperienceDisplayModel>();
  @Output() rowToEdit =
    new EventEmitter<ICandidateWorkExperienceDisplayModel>();
  @Input() workecperienceData: ICandidateWorkExperienceDisplayModel[] = [];
  @Input() hideActions: boolean = false;
  @Input() class: string = "table-in-forms";
  dataSource: MatTableDataSource<ICandidateWorkExperienceDisplayModel>;
  candidateId: number = 0;

  mandatoryColomns: string[] = [
    "company",
    "department",
    "job",
    "startDate",
    "endDate",
    "companySalary",
    "resignationReason",
    "notes",
    "actions",
  ];
  optionalColomns: string[] = [];
  isLoaded: boolean;

  constructor(
    private candidateWorkExperienceService: CandidateWorkExperienceService
  ) {
    super();
    this.candidateId = CandidateService.candidateId$.getValue();

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      this.candidateId > 0
        ? ViewsManager.candidate_Working_Experience_info_sec
        : ViewsManager.add_candidate
    );
  }

  ngOnInit(): void {
    if (this.hideActions == true) {
      let editIndex = this.mandatoryColomns.findIndex(
        (item) => item == "actions"
      );
      this.mandatoryColomns.splice(editIndex, 1);
    }
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {
      this.getList(this.firstPage);
    }
  }

  getList(page: number, pageSize?: number): void {
    this.page.pageIndex = page;
    if (this.candidateId > 0) {
      if (this.workecperienceData?.length == 0) {
        this.candidateWorkExperienceService.workecperienceData$.subscribe((con) => {
          if (con != null) {
            this.dataSource = new MatTableDataSource(con);
          }
        });
      } else {
        this.dataSource.data = this.workecperienceData;
      }
    } else {
      this.dataSource = new MatTableDataSource(this.workecperienceData);
    }
  }

  onDelete(workExperience: ICandidateWorkExperienceDisplayModel): void {
    let msg = `${workExperience.job} (${workExperience.startDate} - ${workExperience.endDate})`;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(workExperience);
    });
  }

  delete(workExperience: ICandidateWorkExperienceDisplayModel) {
    this.afterDelete.emit(workExperience);
  }

  toEdit(training: ICandidateWorkExperienceDisplayModel) {
    this.rowToEdit.emit(training);
  }
}
