import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { ICandidateTrainingDisplayModel } from "app/recruitment/models/candidate/education/training";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { CandidateTrainingService } from "app/recruitment/services/candidate/education/candidate-training.service";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";
@Component({
  selector: "candidate-training-table",
  templateUrl: "./candidate-training-table.component.html",
  styleUrls: ["./candidate-training-table.component.scss"],
})
export class CandidateTrainingTableComponent
  extends AppHelpers
  implements OnInit
{
  @Input() isMatPaginator: boolean = false;
  @Output() afterDelete = new EventEmitter<ICandidateTrainingDisplayModel>();
  @Output() rowToEdit = new EventEmitter<ICandidateTrainingDisplayModel>();
  @Input() trainingData: ICandidateTrainingDisplayModel[] = [];
  @Input() hideActions: boolean = false;
  @Input() class: string = "table-in-forms";

  dataSource: MatTableDataSource<ICandidateTrainingDisplayModel>;
  candidateId: number = 0;

  mandatoryColomns: string[] = [
    "institution",
    "course",
    "startDate",
    "endDate",
    "notes",
    "actions",
  ];
  optionalColomns: string[] = [];
  isLoaded: boolean;

  constructor(private candidateTrainingService: CandidateTrainingService) {
    super();
    this.candidateId = CandidateService.candidateId$.getValue();

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      this.candidateId > 0
        ? ViewsManager.candidate_training_info_sec
        : ViewsManager.add_candidate
    );
  }

  ngOnInit(): void {
    if (this.hideActions == true) {
      let editIndex = this.mandatoryColomns.findIndex(
        (item) => item == "actions"
      );
      this.mandatoryColomns.splice(editIndex, 1);
    }
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {
      this.getList(this.firstPage);
    }
  }

  getList(page: number, pageSize?: number): void {
    this.page.pageIndex = page;
    if (this.candidateId > 0) {
      if (this.trainingData?.length == 0) {
        this.candidateTrainingService.trainingData$.subscribe((con) => {
          if (con != null) {
            this.dataSource = new MatTableDataSource(con);
          }
        });
      } else {
        this.dataSource.data = this.trainingData;
      }
    } else {
      this.dataSource = new MatTableDataSource(this.trainingData);
    }
  }
  onDelete(training: ICandidateTrainingDisplayModel): void {
    let msg = `${training.course} (${training.startDate} - ${training.endDate})`;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(training);
    });
  }

  delete(training) {
    this.afterDelete.emit(training);
  }

  toEdit(training: ICandidateTrainingDisplayModel) {
    this.rowToEdit.emit(training);
  }
}
