import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { IGUIModel } from "app/shared/interfaces/user-views";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";
import { CandidateDocumentsService } from "app/recruitment/services/candidate/work-experience/candidate-documents.service";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import {
  ICandidateDocumentsModel,
  ICandidateDocumentsRequest,
} from "app/recruitment/models/candidate/work-experience/documents";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { candidateDocumentsToDisplay } from "app/recruitment/mapper/candidate/candidate-documents";
@Component({
  selector: "candidate-document-card",
  templateUrl: "./candidate-document-card.component.html",
  styleUrls: ["./candidate-document-card.component.scss"],
})
export class CandidateDocumentCardComponent
  extends AppHelpers
  implements OnInit
{
  isLoaded: boolean = true;
  allowedSections: IGUIModel[];
  @Output() onClickIcon? = new EventEmitter<any>();
  isTableView: boolean = false;
  candidaterelatedRequestId: number = 2;
  candidateId: number = 0;
  documentList: ICandidateDocumentsRequest[] = [];
  @Input() isUpdate: boolean = false;

  constructor(private candidateDocumentsService: CandidateDocumentsService) {
    super();
    this.candidateId = CandidateService.candidateInstanceId$.getValue();
  }
  ngOnInit(): void {}
  override onModulesChanged() {
    this.allowedSections = this.getCustomViewChildren(
      this.getParentViewName,
      ViewsManager.candidate_Documents_info_sec
    );
  }


  override onChangeBusinessAndCompanyId() {
    if (this.candidateId > 0 && this.checkNoBusinessOrCompany()) {
      this.getDocumentByCandidateId();
    }
  }
  click($event) {
    this.onClickIcon.emit($event);
  }
  toggleTable() {
    this.isTableView = !this.isTableView;
  }
  getDocumentByCandidateId() {
    this.isLoaded = false;

    this.candidateDocumentsService
      .getAll(this.candidateId)
      .subscribe((res: IResponse<ICandidateDocumentsModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          let documentTodisplay = candidateDocumentsToDisplay(res.data);
          this.documentList = documentTodisplay;
          this.candidateDocumentsService.documentData$.next(documentTodisplay);
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }
  
  showEdit($event) {
    this.isUpdate = $event;
  }
}
