import { Injectable } from "@angular/core";
import {
  ICandidateMaritalModel,
  ICandidateMaritalRequest,
} from "app/recruitment/models/candidate/general-info/marital-data";
import { IResponse } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { AppClientService } from "app/shared/services/app-client.service";
import { BehaviorSubject } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class CandidateMaritalDataService extends AppClientService {
  maritalData$ = new BehaviorSubject<ICandidateMaritalModel>(undefined);

  constructor() {
    super();
  }

  create(data: ICandidateMaritalRequest) {
    return this.post<IResponse<ICandidateMaritalModel>>(
      "Recruitment/MaritalStatus/Add",
      data
    );
  }

  update(data: ICandidateMaritalRequest) {
    return this.post<IResponse<ICandidateMaritalModel>>(
      "Recruitment/MaritalStatus/Edit",
      data
    );
  }

  getAll(candidateId: number) {
    return this.get<IResponse<ICandidateMaritalModel[]>>(
      "Recruitment/MaritalStatus/GetAll",
      {
        params: {
          $filter: `PersonID eq ${candidateId}`,
          $expand:db.Comm_Marital_Status
        },
      }
    );
  }
}
