import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { ICandidateContactDisplayModel } from "app/recruitment/models/candidate/general-info/contacts";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { CandidateContactsService } from "app/recruitment/services/candidate/general-info/candidate-contacts.service";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";

@Component({
  selector: "candidate-contacts-table",
  templateUrl: "./candidate-contacts-table.component.html",
  styleUrls: ["./candidate-contacts-table.component.scss"],
})
export class CandidateContactsTableComponent
  extends AppHelpers
  implements OnInit
{
  @Input() isMatPaginator: boolean = false;
  @Output() afterDelete = new EventEmitter<ICandidateContactDisplayModel>();
  @Input() contactData: ICandidateContactDisplayModel[] = [];
  @Output() rowToEdit = new EventEmitter<ICandidateContactDisplayModel>();

  dataSource: MatTableDataSource<ICandidateContactDisplayModel> =
    new MatTableDataSource([]);
  candidateId: number = 0;
  mandatoryColomns: string[] = ["contactType", "value", "actions"];
  optionalColomns: string[] = [];
  isLoaded: boolean = false;
  @Input() hideActions: boolean = false;
  @Input() class: string = "table-in-forms";
  constructor(private candidateContactsService: CandidateContactsService) {
    super();
    this.candidateId = CandidateService.candidateId$.getValue();

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      this.candidateId > 0
        ? ViewsManager.candidate_contacts_sec
        : ViewsManager.add_candidate
    );
  }

  ngOnInit(): void {
    if (this.hideActions == true) {
      let editIndex = this.mandatoryColomns.findIndex(
        (item) => item == "actions"
      );
      this.mandatoryColomns.splice(editIndex, 1);
    }
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {
      this.getList(this.firstPage);
    }
  }

  getList(page: number, pageSize?: number): void {
    this.page.pageIndex = page;
    if (this.candidateId > 0) {
      if (this.contactData?.length == 0) {

        this.candidateContactsService.contactData$.subscribe((con) => {
          if (con != null) {
            this.dataSource = new MatTableDataSource(con);
          }
        });
      } else {
        this.dataSource.data = this.contactData;
      }
    } else {
      this.dataSource = new MatTableDataSource(this.contactData);
    }
  }

  onDelete(contactInfo: ICandidateContactDisplayModel): void {
    let msg = contactInfo.contactType + " - " + contactInfo.value;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) {
        this.delete(contactInfo);
      }
    });
  }

  delete(contact) {
    this.afterDelete.emit(contact);

  }

  toEdit(contact: ICandidateContactDisplayModel) {
    this.rowToEdit.emit(contact);
  }
}
