import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { ICandidateEducationDisplayModel } from "app/recruitment/models/candidate/education/education";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { CandidateEducationService } from "app/recruitment/services/candidate/education/candidate-education.service";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";
@Component({
  selector: "candidate-education-table",
  templateUrl: "./candidate-education-table.component.html",
  styleUrls: ["./candidate-education-table.component.scss"],
})
export class CandidateEducationTableComponent
  extends AppHelpers
  implements OnInit
{
  @Input() isMatPaginator: boolean = false;
  @Output() afterDelete = new EventEmitter<ICandidateEducationDisplayModel>();

  @Input() educationData: ICandidateEducationDisplayModel[] = [];
  @Input() hideActions: boolean = false;
  @Input() class: string = "table-in-forms";
  @Output() rowToEdit = new EventEmitter<ICandidateEducationDisplayModel>();
  dataSource: MatTableDataSource<ICandidateEducationDisplayModel>;
  candidateId: number = 0;
  isLoaded: boolean = false;

  mandatoryColomns: string[] = [
    "institution",
    "academicQualifications",
    "major",
    "year",
    "grade",
    "actions",
  ];
  optionalColomns: string[] = [];

  constructor(private candidateEducationService: CandidateEducationService) {
    super();
    this.candidateId = CandidateService.candidateId$.getValue();

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      this.candidateId > 0
        ? ViewsManager.candidate_education_info_sec
        : ViewsManager.add_candidate
    );
  }

  ngOnInit(): void {
    if (this.hideActions == true) {
      let editIndex = this.mandatoryColomns.findIndex(
        (item) => item == "actions"
      );
      this.mandatoryColomns.splice(editIndex, 1);
    }
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {
      this.getList(this.firstPage);
    }
  }

  getList(page: number, pageSize?: number): void {
    this.page.pageIndex = page;
    if (this.candidateId > 0) {
      if (this.educationData?.length == 0) {
        this.candidateEducationService.educationData$.subscribe((con) => {
          if (con != null) {
            this.dataSource = new MatTableDataSource(con);
          }
        });
      } else {
        this.dataSource.data = this.educationData;
      }
    } else {
      this.dataSource = new MatTableDataSource(this.educationData);
    }
  }

  onDelete(education: ICandidateEducationDisplayModel): void {
    let msg = education.academicQualifications + " - " + education.grade;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(education);
    });
  }

  toEdit(education: ICandidateEducationDisplayModel) {
    this.rowToEdit.emit(education);
  }

  delete(education: ICandidateEducationDisplayModel) {
    this.afterDelete.emit(education);
  }
}
