import { Component, Input, OnInit } from "@angular/core";
import { candidateContactToDisplay } from "app/recruitment/mapper/candidate/candidate-contact-info";
import { ICandidateContactDisplayModel, ICandidateContactModel } from "app/recruitment/models/candidate/general-info/contacts";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { CandidateContactsService } from "app/recruitment/services/candidate/general-info/candidate-contacts.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "candidate-contacts-card",
  templateUrl: "./candidate-contacts-card.component.html",
  styleUrls: ["./candidate-contacts-card.component.scss"],
})
export class CandidateContactsCardComponent
  extends AppHelpers
  implements OnInit
{
  isLoaded: boolean = false;
  @Input() isTableView: boolean = false;
  contactData: ICandidateContactDisplayModel;
  constructor(private candidateContactsService: CandidateContactsService) {
    super();
    this.candidateId = CandidateService.candidateId$.getValue();
  }
  candidateId: number = 0;

  ngOnInit(): void {}
  afterDelete(contact) {}
  override onChangeBusinessAndCompanyId() {
    if (this.candidateId > 0 && this.checkNoBusinessOrCompany()) {
      this.getAll();
    }
  }
  getAll() {
    this.candidateContactsService
      .getAll(this.candidateId)
      .subscribe((res: IResponse<ICandidateContactModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          let contactTodusplay = candidateContactToDisplay(res.data);

          this.contactData = contactTodusplay[0];
          this.candidateContactsService.contactData$.next(contactTodusplay);
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }
}
