import {
  ICandidateDocumentsDisplayModel,
  ICandidateDocumentsModel,
} from "app/recruitment/models/candidate/work-experience/documents";
import { msgTranslate } from "app/shared/resources/helpers-functions";

export function candidateDocumentsToDisplay(
  data: ICandidateDocumentsModel[]
): ICandidateDocumentsDisplayModel[] {
  return data.map((document) => {
    return <ICandidateDocumentsDisplayModel>{
      ...document,
      attachmentID:document.comm_Attachment?.id,
      attachmentType: msgTranslate(
        document.comm_Attachment.comm_Attachment_Type?.ar_Name,
        document.comm_Attachment.comm_Attachment_Type?.en_Name
      ),
      attachmentTypeID:document.comm_Attachment.comm_Attachment_Type.id,
      attachmentName: document.comm_Attachment?.en_Name,
      attachmentPath: document.comm_Attachment.path
        ? "/" + document.comm_Attachment.path
        : "",
        attachmentMime:document.comm_Attachment.mime,
        attachmentSize:document.comm_Attachment.size,
        lastModified:document.comm_Attachment.last_Update_Date,
    };
  });
}
