import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import {
  ICandidateDocumentsDisplayModel,
  ICandidateDocumentsModel,
} from "app/recruitment/models/candidate/work-experience/documents";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { CandidateDocumentsService } from "app/recruitment/services/candidate/work-experience/candidate-documents.service";
import { IRowActionsModel } from "app/shared/interfaces/user-views";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";
@Component({
  selector: "candidate-document-table",
  templateUrl: "./candidate-document-table.component.html",
  styleUrls: ["./candidate-document-table.component.scss"],
})
export class CandidateDocumentTableComponent
  extends AppHelpers
  implements OnInit
{
  @Input() isMatPaginator: boolean = false;
  @Output() afterDelete = new EventEmitter<any>();
  dataSource: MatTableDataSource<ICandidateDocumentsDisplayModel>;
  documentsRowFunctions: IRowActionsModel = { basic: [], dropdown: [] };
  @Input() documentData: ICandidateDocumentsDisplayModel[] = [];
  @Output() rowToEdit = new EventEmitter<ICandidateDocumentsDisplayModel>();

  mandatoryColomns: string[] = ["attachmentType", "attachment", "actions"];
  optionalColomns: string[] = [];
  isLoaded: boolean;
  @Input() hideActions: boolean = false;
  candidateId: number = 0;

  constructor(private candidateDocumentsService: CandidateDocumentsService) {
    super();
    this.candidateId = CandidateService.candidateId$.getValue();

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      this.candidateId > 0
        ? ViewsManager.candidate_Documents_info_sec
        : ViewsManager.add_candidate
    );
  }

  ngOnInit(): void {
    if (this.hideActions == true) {
      let editIndex = this.mandatoryColomns.findIndex(
        (item) => item == "actions"
      );
      this.mandatoryColomns.splice(editIndex, 1);
    }
    this.getDocumentRowFunctions();
  }

  getDocumentRowFunctions() {
    this.rowFunctions?.basic.forEach((e) => {
      if (e.name.toLowerCase() == "delete" || e.name.toLowerCase() == "edit")
        this.documentsRowFunctions?.basic.push(e);
    });
  }
  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {
      this.getList();
    }
  }
  getList(): void {
    if (this.candidateId > 0) {
      if (this.documentData?.length == 0) {
        this.candidateDocumentsService.documentData$.subscribe((con) => {
          if (con != null) {
            this.dataSource = new MatTableDataSource(con);
          }
        });
      } else {
        this.dataSource.data = this.documentData;
      }
    } else {
      this.dataSource = new MatTableDataSource(this.documentData);
    }
  }
  onDelete(documents: ICandidateDocumentsDisplayModel): void {
    let msg = documents.attachmentName;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(documents);
    });
  }

  delete(document: ICandidateDocumentsDisplayModel) {
    this.afterDelete.emit(document);
  }
  toEdit(documents: ICandidateDocumentsDisplayModel) {
    this.rowToEdit.emit(documents);
  }
}
