import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { IGUIModel } from "app/shared/interfaces/user-views";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";
import {
  ICandidateReferenceDisplayModel,
  ICandidateReferenceModel,
} from "app/recruitment/models/candidate/work-experience/reference";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { CandidateRefrenceService } from "app/recruitment/services/candidate/work-experience/candidate-refrence.service";
import { candidateReferenceToDisplay } from "app/recruitment/mapper/candidate/candidate-reference";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
@Component({
  selector: "candidate-reference-card",
  templateUrl: "./candidate-reference-card.component.html",
  styleUrls: ["./candidate-reference-card.component.scss"],
})
export class CandidateReferenceCardComponent
  extends AppHelpers
  implements OnInit
{
  isLoaded: boolean = true;
  allowedSections: IGUIModel[];
  @Output() onClickIcon? = new EventEmitter<any>();
  isTableView: boolean = false;
  candidateId: number = 0;
  refrenceData: ICandidateReferenceDisplayModel;
  @Input() isUpdate: boolean = false;

  constructor(private candidateRefrenceService: CandidateRefrenceService) {
    super();
    this.candidateId = CandidateService.candidateInstanceId$.getValue();
  }
  ngOnInit(): void {}
  override onModulesChanged() {
    this.allowedSections = this.getCustomViewChildren(
      this.getParentViewName,
      ViewsManager.candidate_Reference_info_sec
    );
  }

  override onChangeBusinessAndCompanyId() {
    if (this.candidateId > 0 && this.checkNoBusinessOrCompany()) {
      this.getAll();
    }
  }

  getAll() {
    this.isLoaded = false;
    this.candidateRefrenceService
      .getAll(CandidateService.candidateInstanceId$.getValue())
      .subscribe((res: IResponse<ICandidateReferenceModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          let refrenceTodisplay = candidateReferenceToDisplay(res.data);
          this.refrenceData = refrenceTodisplay[0];

          this.candidateRefrenceService.refrenceData$.next(refrenceTodisplay);
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }


  click($event) {
    this.onClickIcon.emit($event);
  }
  toggleTable() {
    this.isTableView = !this.isTableView;
  }

  showEdit($event) {
    this.isUpdate = $event;
  }
}
