import { Injectable } from "@angular/core";
import {
  ICandidateEducationDeleteParams,
  ICandidateEducationDisplayModel,
  ICandidateEducationEditRequest,
  ICandidateEducationModel,
  ICandidateEducationRequest,
} from "app/recruitment/models/candidate/education/education";
import { IResponse } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { AppClientService } from "app/shared/services/app-client.service";
import { BehaviorSubject } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class CandidateEducationService extends AppClientService {
  educationData$=new BehaviorSubject<ICandidateEducationDisplayModel[]>(undefined)
  constructor() {
    super();
  }

  create(data: ICandidateEducationRequest) {
    return this.post<IResponse<ICandidateEducationModel>>(
      "Recruitment/Education/Add",
      data
    );
  }

  update(data: ICandidateEducationEditRequest) {
    return this.post<IResponse<ICandidateEducationModel>>(
      "Recruitment/Education/Edit",
      data
    );
  }

  getAll(candidateId: number) {
    return this.get<IResponse<ICandidateEducationModel[]>>(
      "Recruitment/Education/GetAll",
      {
        params: {
          $filter: `PersonID eq ${candidateId}`,
          $expand: `${db.comm_Institution},${db.comm_Education_Degree},${db.comm_Grade_Type}`,
        },
      }
    );
  }

  Delete(params: ICandidateEducationDeleteParams) {
    return this.delete<IResponse>("Recruitment/Education/Delete", {
      params: params,
    });
  }
}
