import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { ICandidateReferenceDisplayModel } from "app/recruitment/models/candidate/work-experience/reference";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { CandidateRefrenceService } from "app/recruitment/services/candidate/work-experience/candidate-refrence.service";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";

@Component({
  selector: "candidate-reference-table",
  templateUrl: "./candidate-reference-table.component.html",
  styleUrls: ["./candidate-reference-table.component.scss"],
})
export class CandidateReferenceTableComponent
  extends AppHelpers
  implements OnInit
{
  @Input() isMatPaginator: boolean = false;
  @Output() afterDelete = new EventEmitter<ICandidateReferenceDisplayModel>();
  @Output() rowToEdit = new EventEmitter<ICandidateReferenceDisplayModel>();
  @Input() refrenceData: ICandidateReferenceDisplayModel[] = [];
  @Input() hideActions: boolean = false;
  @Input() class: string = "table-in-forms";
  dataSource: MatTableDataSource<ICandidateReferenceDisplayModel>;
  candidateId: number = 0;

  mandatoryColomns: string[] = [
    "reference",
    "position",
    "mobileNumber",
    "email",
    "notes",
    "actions",
  ];
  optionalColomns: string[] = [];
  isLoaded: boolean;

  constructor(private candidateRefrenceService: CandidateRefrenceService) {
    super();
    this.candidateId = CandidateService.candidateId$.getValue();

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      this.candidateId > 0
        ? ViewsManager.candidate_Reference_info_sec
        : ViewsManager.add_candidate
    );
  }

  ngOnInit(): void {
    if (this.hideActions == true) {
      let editIndex = this.mandatoryColomns.findIndex(
        (item) => item == "actions"
      );
      this.mandatoryColomns.splice(editIndex, 1);
    }
  }
  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {
      this.getList(this.firstPage);
    }
  }

  getList(page: number, pageSize?: number): void {
    this.page.pageIndex = page;
    if (this.candidateId > 0) {
      if (this.refrenceData?.length == 0) {
        this.candidateRefrenceService.refrenceData$.subscribe((con) => {
          if (con != null) {
            this.dataSource = new MatTableDataSource(con);
          }
        });
      } else {
        this.dataSource.data = this.refrenceData;
      }
    } else {
      this.dataSource = new MatTableDataSource(this.refrenceData);
    }
  }
  onDelete(reference: ICandidateReferenceDisplayModel): void {
    let msg = `${reference.company} - ${reference.reference}`;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(reference);
    });
  }

  delete(reference: ICandidateReferenceDisplayModel) {
    this.afterDelete.emit(reference);
  }

  toEdit(training: ICandidateReferenceDisplayModel) {
    this.rowToEdit.emit(training);
  }
}
