import { Component, OnInit } from "@angular/core";
import { CandidateMaritalDataService } from "app/recruitment/services/candidate/general-info/candidate-marital-data.service";
import { ICandidateMaritalModel } from "app/recruitment/models/candidate/general-info/marital-data";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
@Component({
  selector: "candidate-basic-marital-card",
  templateUrl: "./candidate-basic-marital-card.component.html",
  styleUrls: ["./candidate-basic-marital-card.component.scss"],
})
export class CandidateBasicMaritalCardComponent
  extends AppHelpers
  implements OnInit
{
  isLoaded: boolean =false;
  maritalData: ICandidateMaritalModel;
  candidateId: number = 0;

  constructor(
    private candidateMaritalDataService: CandidateMaritalDataService,    private candidateService: CandidateService,

  ) {
    super();
    this.candidateId = CandidateService.candidateId$.getValue();

  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId() {
    if (this.candidateId > 0 && this.checkNoBusinessOrCompany()) {
      this.getMaritalStatus();
    }
  }

  getMaritalStatus() {
    this.candidateMaritalDataService
      .getAll(this.candidateId)
      .subscribe((res: IResponse<ICandidateMaritalModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
            this.candidateMaritalDataService.maritalData$.next(res.data[0]);
            this.maritalData=res.data[0];
            this.isLoaded=true;
        } else this.errorList(res.message.messages);
      });
  }
}
