import { Injectable } from "@angular/core";
import {
  ICandidateReferenceDisplayModel,
  ICandidateReferenceModel,
  ICandidateReferenceRequest,
} from "app/recruitment/models/candidate/work-experience/reference";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { BehaviorSubject } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class CandidateRefrenceService extends AppClientService {
  constructor() {
    super();
  }
  refrenceData$ = new BehaviorSubject<ICandidateReferenceDisplayModel[]>(
    undefined
  );

  update(data: ICandidateReferenceRequest) {
    return this.post<IResponse<ICandidateReferenceModel>>(
      "Recruitment/Refrence/Edit",
      data
    );
  }

  getAll(candidateId: number) {
    return this.get<IResponse<ICandidateReferenceModel[]>>(
      "Recruitment/Refrence/GetAll",
      {
        params: {
          $filter: `Person_Instance_ID eq ${candidateId}`,
        },
      }
    );
  }

  Delete(id: number) {
    return this.delete<IResponse>("Recruitment/Refrence/Delete", {
      params: { ID: id },
    });
  }
}
