import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { ICandidateCertificationDisplayModel } from "app/recruitment/models/candidate/education/certifications";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { CandidateCertificationsService } from "app/recruitment/services/candidate/education/candidate-certifications.service";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";
@Component({
  selector: "candidate-certification-table",
  templateUrl: "./candidate-certification-table.component.html",
  styleUrls: ["./candidate-certification-table.component.scss"],
})
export class CandidateCertificationTableComponent
  extends AppHelpers
  implements OnInit
{
  @Input() isMatPaginator: boolean = false;
  @Output() afterDelete =
    new EventEmitter<ICandidateCertificationDisplayModel>();
  @Output() rowToEdit = new EventEmitter<ICandidateCertificationDisplayModel>();
  @Input() certificationData: ICandidateCertificationDisplayModel[] = [];
  @Input() hideActions: boolean = false;
  @Input() class: string = "table-in-forms";
  dataSource: MatTableDataSource<ICandidateCertificationDisplayModel>;
  candidateId: number = 0;

  mandatoryColomns: string[] = [
    "certification",
    "location",
    "startDate",
    "endDate",
    "notes",
    "actions",
  ];
  optionalColomns: string[] = [];
  isLoaded: boolean = false;

  constructor(
    private cndidateCertificationsService: CandidateCertificationsService
  ) {
    super();
    this.candidateId = CandidateService.candidateId$.getValue();

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      this.candidateId > 0
        ? ViewsManager.candidate_certification_info_sec
        : ViewsManager.add_candidate
    );
  }

  ngOnInit(): void {
    if (this.hideActions == true) {
      let editIndex = this.mandatoryColomns.findIndex(
        (item) => item == "actions"
      );
      this.mandatoryColomns.splice(editIndex, 1);
    }
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {
      this.getList(this.firstPage);
    }
  }

  getList(page: number, pageSize?: number): void {
    this.page.pageIndex = page;
    if (this.candidateId > 0) {
      if (this.certificationData?.length == 0) {
        this.cndidateCertificationsService.certificationsData$.subscribe(
          (con) => {
            if (con != null) {
              this.dataSource = new MatTableDataSource(con);
            }
          }
        );
      } else {
        this.dataSource.data = this.certificationData;
      }
    } else {
      this.dataSource = new MatTableDataSource(this.certificationData);
    }
  }

  onDelete(certification: ICandidateCertificationDisplayModel): void {
    let msg = `${certification.certification} (${certification.startDate} - ${certification.endDate})`;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(certification);
    });
  }
  delete(certification: ICandidateCertificationDisplayModel) {
    this.afterDelete.emit(certification);
  }

  toEdit(training: ICandidateCertificationDisplayModel) {
    this.rowToEdit.emit(training);
  }
}
