import { Component, Input, OnInit } from "@angular/core";
import { ICandidateMilitaryModel } from "app/recruitment/models/candidate/general-info/military-data";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { CandidateMilitaryDataService } from "app/recruitment/services/candidate/general-info/candidate-military-data.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "candidate-basic-military-card",
  templateUrl: "./candidate-basic-military-card.component.html",
  styleUrls: ["./candidate-basic-military-card.component.scss"],
})
export class CandidateBasicMilitaryCardComponent
  extends AppHelpers
  implements OnInit
{
  isLoaded: boolean = false;
  candidateId: number = 0;
  MilitaryStatus: ICandidateMilitaryModel;

  constructor(
    private candidateMilitaryDataService: CandidateMilitaryDataService
  ) {
    super();
    this.candidateId = CandidateService.candidateId$.getValue();
  }

  ngOnInit(): void {}
  override onChangeBusinessAndCompanyId() {
    if (this.candidateId > 0 && this.checkNoBusinessOrCompany()) {
      this.getMilitaryStatus();
    }
  }
  getMilitaryStatus() {
    this.candidateMilitaryDataService
      .getAll(this.candidateId)
      .subscribe((res: IResponse<ICandidateMilitaryModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.MilitaryStatus = res.data[0];
          this.candidateMilitaryDataService.militaryData$.next(res.data[0]);
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }
}
