import { Injectable } from "@angular/core";
import {
  ICandidateCertificationDisplayModel,
  ICandidateCertificationModel,
  ICandidateCertificationRequest,
} from "app/recruitment/models/candidate/education/certifications";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { BehaviorSubject } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class CandidateCertificationsService extends AppClientService {
  certificationsData$=new BehaviorSubject<ICandidateCertificationDisplayModel[]>(undefined)

  constructor() {
    super();
  }

  create(data: ICandidateCertificationRequest) {
    return this.post<IResponse<ICandidateCertificationModel>>(
      "Recruitment/Certification/Add",
      data
    );
  }

  update(data: ICandidateCertificationRequest) {
    return this.post<IResponse<ICandidateCertificationModel>>(
      "Recruitment/Certification/Edit",
      data
    );
  }

  getAll(candidateId: number) {
    return this.get<IResponse<ICandidateCertificationModel[]>>(
      "Recruitment/Certification/GetAll",
      {
        params: {
          $filter: `Person_Instance_ID eq ${candidateId}`,
        },
      }
    );
  }

  Delete(id: number) {
    return this.delete<IResponse>("Recruitment/Certification/Delete", {
      params: { ID: id },
    });
  }
}
