import { Component, HostListener, OnInit, ViewChild } from "@angular/core";
import { MatTabGroup } from "@angular/material/tabs";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { DeviceDetectorService } from "ngx-device-detector";
import { Storage } from "app/shared/resources/local-storage";
import { Languages } from "app/shared/enums/languages.enum";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { ICandidateListModel } from "app/recruitment/models/candidate/candidate";

@Component({
  selector: "app-view-candidate",
  templateUrl: "./view-candidate.component.html",
  styleUrls: ["./view-candidate.component.scss"],
})
export class ViewCandidateComponent extends AppHelpers implements OnInit {
  candidateId: number = 0;
  @ViewChild("tabGroup") tabGroup: MatTabGroup;
  activeTab: number = 0;
  customBorderWidth: string = "100%";
  isOnScrollToSection: boolean = false;
  firstScroll: boolean = true;
  candidate: ICandidateListModel;
  candidateLoaded: boolean = false;
  constructor(
    public deviceService: DeviceDetectorService,
    public candidateService: CandidateService
  ) {
    super();
    this.activatedRoute.params.subscribe((params) => {
      this.candidateId = Number(params.candidateId ?? 0);
      CandidateService.candidateId$.next(this.candidateId);
      this.onChangeBusinessAndCompanyId();
    });
  }

  ngOnInit(): void {
    this.candidateService.candidate$.subscribe((candidate) => {
      this.candidate = candidate;
      this.candidateLoaded = true;
    });
  }
  override onChangeBusinessAndCompanyId() {
    this.candidateService.candidate$.next(null);
    if (this.checkNoBusinessOrCompany() && this.candidateId > 0) {
      this.candidateService.getCandidatShared(this.candidateId);
    }
    setTimeout(() => {
      this.firstScroll = false;
    }, 2000);
  }
  onAddNew() {
    this.ngOnDestroy();
  }

  changeCandidateId(candidateId: number) {
    this.candidateId = candidateId;
  }

  override onModulesChanged() {
    setTimeout(() => {
      this.updateMatTabsPosition();
    }, 0);
  }

  scrollToSection(event, sectionId: string) {
    this.isOnScrollToSection = true;
    event.preventDefault();
    const element: any = document.getElementById(sectionId);
    if (element) {
      let top = element.offsetTop;
      window.scroll({ top: top, behavior: "smooth" });
    }
    setTimeout(() => {
      this.isOnScrollToSection = false;
    }, 1000);
  }

  updateMatTabsPosition() {
    let tabPositions =
      this.tabGroup._elementRef.nativeElement.getBoundingClientRect();
    if (
      Storage.getLocalLang == Languages.en &&
      (tabPositions.left > 100 ||
        this.tabGroup._elementRef.nativeElement.offsetTop + 120)
    ) {
      this.customBorderWidth = `${window.innerWidth - tabPositions.left}px`;
    } else if (
      Storage.getLocalLang == Languages.ar &&
      (window.innerWidth - tabPositions.left - tabPositions.width > 100 ||
        this.tabGroup._elementRef.nativeElement.offsetTop + 120)
    ) {
      this.customBorderWidth = `${
        window.innerWidth -
        (window.innerWidth - tabPositions.left - tabPositions.width)
      }px`;
    } else this.customBorderWidth = "100%";
  }

  @HostListener("window:scroll", ["$event"])
  onScroll($event) {
    if (this.firstScroll) {
      window.scrollTo(0, 0);
      this.activeTab = 0;
      setTimeout(() => {
        this.firstScroll = false;
      }, 2000);
    }

    let scrollTop = window.pageYOffset;
    const element = document.getElementById("top-tools");

    if (scrollTop >= this.tabGroup._elementRef.nativeElement.offsetTop + 120) {
      this.tabGroup._elementRef.nativeElement.classList.add("nav-fixed");
      element.classList.add("mt-2");
    } else {
      this.tabGroup._elementRef.nativeElement.classList.remove("nav-fixed");
      element.classList.remove("mt-2");
    }

    this.updateMatTabsPosition();

    if (!this.isOnScrollToSection) {
      // change active tab
      this.customViews?.forEach((sec, index) => {
        const element = document.getElementById(sec.viewName);
        if (scrollTop >= element.offsetTop) {
          this.activeTab = index;
        }
      });
    }
  }

  @HostListener("window:resize", ["$event"])
  onResize() {
    // this.checkForOverflow();
    this.updateMatTabsPosition();
  }
  ngOnDestroy(): void {
    CandidateService.candidateId$.next(null);
    this.candidateService.cancelRequest$.next();
    this.candidateService.candidate$.next(null);

  }
}
